<template>
  <div class="container">
    <section class="hero is-primary">
      <div class="hero-body">
        <p class="title">
          How to add a New Stage
        </p>
        <div>Last updated for Version 1.3.0</div>
      </div>
    </section>
    <section class="section main-part">
      <p class="title">Stage Rooms</p>
      <p>
        Each stage in PFE is created with a room resource.
        Start by duplicating the <code>rm_stage_template</code> room, and name the new room whatever you want.
        For this tutorial, we'll name it <code>rm_castle</code>.
        It's a good idea to make a corresponding group to organize all assets related to the stage, but it's not required.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_1.png" class="image"/>
      <br/>
      <p>
        Double click on the room to open it in the room editor.
        The sidebar should contain tabs for Layers and Room Properties.
        If you don't see these tabs, click on the "Room" dropdown on the top bar and select "Reset Windows On Current Desktop":
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_2.png" class="image"/>
      <br/>
      <p>
        There are 9 layers from the template that all stages should have:
      </p>
      <ol>
        <li>
          <strong>Top_Layer:</strong> 
          The layer for <router-link to="/docs/obj_game">obj_game</router-link>
        </li>
        <li>
          <strong>Player_Front:</strong> 
          The layer for <router-link to="/docs/obj_player_renderer_foreground">obj_player_renderer_foreground</router-link>
        </li>
        <li>
          <strong>VFX_Layer:</strong> 
          The default layer that visual effects are created on
        </li>
        <li>
          <strong>Game_Layer:</strong> 
          The layer for 
          <router-link to="/docs/obj_player_renderer">obj_player_renderer</router-link>, 
          <router-link to="/docs/obj_player_spawner">obj_player_spawner</router-link>, and 
          <router-link to="/docs/obj_respawn_platform">obj_respawn_platform</router-link>
        </li>
        <li>
          <strong>VFX_Layer_Below:</strong>
          The layer that certain visual effects are created on
        </li>
        <li>
          <strong>Stage:</strong>
          The layer for all "stage element" instances - blocks, platforms, ledges, etc.
        </li>
        <li>
          <strong>Player_Back:</strong> 
          The layer for <router-link to="/docs/obj_player_renderer_background">obj_player_renderer_background</router-link>
        </li>
        <li>
          <strong>Stage_Background:</strong> 
          The layer for <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>
        </li>
        <li>
          <strong>Background:</strong>
          A single color layer below all other layers. (The color on this layer won't be visible unless 
          <router-link to="/docs/game_surface_enable">game_surface_enable</router-link> is turned off)
        </li>
      </ol>
      <br/>
      <img src="@/assets/Tutorials/tb3_4.png" class="image"/>
      <br/>
      <p>
        If you want to change the size of the stage, you can adjust the width and height in the Properties tab.
        If you change the width, make sure to re-center all of the instances in the room so the blastzones stay symmetrical.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_6.png" class="image"/>
      <br/>

      <p class="title">Stage Elements</p>
      <p>
        There are 11 pre-made stage elements:
      </p>
      <ol>
        <li>
          <router-link to="/docs/obj_solid">obj_solid</router-link>:
          A solid block that players cannot pass through
        </li>
        <li>
          <router-link to="/docs/obj_slope">obj_slope</router-link>:
          A solid slope
        </li>
        <li>
          <router-link to="/docs/obj_solid_moving">obj_solid_moving</router-link>:
          A solid block that moves between
          <router-link to="/docs/obj_block_moving_point">obj_block_moving_point</router-link> instances, configured in the <code>Variables</code> tab
        </li>
        <li>
          <router-link to="/docs/obj_platform">obj_platform</router-link>:
          A jumpthrough platform
        </li>
        <li>
          <router-link to="/docs/obj_platform_blinker">obj_platform_blinker</router-link>:
          A platform that appears and disappears on a timer
        </li>
        <li>
          <router-link to="/docs/obj_platform_moving">obj_platform_moving</router-link>:
          A platform that moves between
          <router-link to="/docs/obj_block_moving_point">obj_block_moving_point</router-link> instances, configured in the <code>Variables</code> tab
        </li>
        <li>
          <router-link to="/docs/obj_block_moving_point">obj_block_moving_point</router-link>:
          A point that moving blocks/platforms can move towards. The variables in the <code>Variables</code> tab of each instance must be set up individually
        </li>
        <li>
          <router-link to="/docs/obj_ledge">obj_ledge</router-link>:
          A ledge that players can grab. The direction of the ledge can be changed by flipping the instance horizontally
        </li>
        <li>
          <router-link to="/docs/obj_player_spawner">obj_player_spawner</router-link>:
          A location that players will spawn at when the match starts.
          The <strong>Instance Creation Order</strong> of the player spawners determines which players will spawn on which spawner
        </li>
        <li>
          <router-link to="/docs/obj_respawn_platform">obj_respawn_platform</router-link>:
          A location that players will respawn at
        </li>
        <li>
          <router-link to="/docs/obj_item_spawn_zone">obj_item_spawn_zone</router-link>:
          A zone where items can spawn in
        </li>
      </ol>
      <br/>
      <img src="@/assets/Tutorials/tb3_3.png" class="image"/>
      <br/>
      <p>
        To add instances to a given layer, simply click on the layer and then drag the object from the Asset Browser into the room.
        You can delete instances by clicking on them and pressing the Backspace or Delete.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_5.gif" class="image"/>
      <br/>
      <p>
        Here's an example stage layout that you could make:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_7.png" class="image"/>
      <br/>

      <p class="title">Adding the Stage to the Stage Select Screen</p>
      <p>
        The stage select screen is a room named <code>rm_sss</code> in <code>Menus > Stage Select Screen</code>.
        (You can also use the Ctrl + T shortcut to search for it!) 
        Once you have the room open, navigate to the "Stage_Zones" layer. 
      </p>
      <br/>
      <p>
        Due to the way the stage select is set up, it would be difficult to fit another stage, so for this tutorial we'll just
        change one of the existing <router-link to="/docs/obj_sss_zone">obj_sss_zone</router-link> instances instead of adding a new one.
        Double click on an instance, and then click on the "Variables" button to open the Variables tab.
        Change the <code>stage</code> variable to the name of the new stage room:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_8.png" class="image"/>
      <br/>
      <p>
        The stage won't have any thumbnail or name right now, but you should still be able to select it, and it'll take you to the new room.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_9.png" class="image"/>
      <br/>

      <p class="title">Stage Data</p>
      <p>
        The thumbnail and name of each stage is set in the <router-link to="/docs/stage_data_get">stage_data_get</router-link> script.
        Add a <code>case</code> for the new stage as shown:
      </p>
      <br/>
      <p>
        For this tutorial we're reusing the thumbnail image from the premade stages, but you can make your own and set it here.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_10.png" class="image"/>
      <br/>
      <p>
        We also need to add the stage to the list of stages that can be selected by the Random button.
        This list is located in the function <router-link to="/docs/stage_choose_random">stage_choose_random</router-link>:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_11.png" class="image"/>
      <br/>

      <p class="title">Graphics</p>
      <p>
        Now that the stage is fully functional, we can start adding graphics.
        There are three main ways to add graphics to stages:
      </p>
      <ol>
        <li>Creating asset layers and dragging sprites directly into the room (for non-animating sprites)</li>
        <li>Adding code in <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link> (for parallax backgrounds)</li>
        <li>Adding custom objects to the room that draw sprites</li>
      </ol>
      <br/>
      <p>
        We'll cover the first two methods in this tutorial, since the third method is very open-ended.
      </p>
      <p>
        For the first method, create a new asset layer in the room. 
        Make sure the name of the layer starts with "Asset", so the engine can identify it properly.
        The exact position of the asset layer in the layer order will be different depending on what sprites you plan to put on it,
        but for this tutorial we'll place it just above the "Stage" layer.
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_12.png" class="image"/>
      <br/>
      <p>
        Next, drag the sprites you want to use for the stage onto the asset layer.
        If you're making the "Castle" stage, you can use the following sprites:
      </p>
      <br/>
      <div class="columns">
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">spr_castle_stone</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/spr_castle_stone.png" class="image"/></div>
            </div>
          </div>
        </div>
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">spr_castle_stone_plat</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/spr_castle_stone_plat.png" class="image"/></div>
            </div>
          </div>
        </div>
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">spr_castle_stone_slope</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/spr_castle_stone_slope.png" class="image"/></div>
            </div>
          </div>
        </div>
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">spr_castle_pillar</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/spr_castle_pillar.png" class="image"/></div>
            </div>
          </div>
        </div>
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">spr_castle_window</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/spr_castle_window.png" class="image"/></div>
            </div>
          </div>
        </div>
      </div>
      <br/>
      <p>
        You'll need to scale the sprites 2x and overlap some of the stone sprites to make them properly cover the left side:
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_13.png" class="image"/>
      <br/>
      <p>
        For the right side, make a new asset layer, and place it above the "Game_Layer". 
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_14.png" class="image"/>
      <br/>
      <p>
        Once you have everything in place, you can hide the "Stage" layer by clicking on the eye symbol next to the layer.
        Run the game, and you should see the stage graphics!
      </p>
      <br/>
      <img src="@/assets/Tutorials/tb3_15.gif" class="image"/>
      <br/>

      <p class="title">Backgrounds</p>
      <p>
        To add backgrounds to the stage, go to the Create Event of <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>.
        Each stage that has backgrounds needs a <code>case</code> in the "Stage-specific values" <code>switch</code> statement.
      </p>
      <br/>
      <p>
        For the "Castle" stage, we'll add two backgrounds - a distant non-moving sky background, and a parallax mountain range background.
        Download the following sprites, and name them <code>back_castle_distant</code> and <code>back_castle_mountains</code> respectively.
        <strong>Make sure to set the origins to Middle Center!</strong>
      </p>
      <br/>
      <div class="columns">
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">back_castle_distant</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/back_castle_distant.png" class="image"/></div>
            </div>
          </div>
        </div>
        <div class="column">
          <div class="card">
            <header class="card-header"><p class="card-header-title">back_castle_mountains</p></header>
            <div class="card-image">
              <div class="section"><img src="@/assets/Tutorials/back_castle_mountains.png" class="image"/></div>
            </div>
          </div>
        </div>
      </div>
      <br/>
      <p>
        Add a new <code>case</code> for <code>rm_Castle</code> with this code: 
      </p>
      <br/>
      <pre>case rm_Castle:
  background = 
    [
    background_define(back_castle_distant, 0, 0, 2, 0, 0),
    background_define(back_castle_mountains, 0, 0, 2, 0.1, 0.1),
    back_clear,
    ];
  break;</pre>
      <br/>
      <p>
        This code sets the variable <code>background</code> to an array with 
        <router-link to="/docs/background_define">background_define</router-link> calls for both background sprites.
        The sprites are half-sized, so they need a scale of 2 to properly fill the screen.
        The distant background has NO parallax (<code>0, 0</code>), while the mountains have a small amount (<code>0.1, 0.1</code>).
      </p>
      <br/>
      <p>
        The <router-link to="/docs/back_clear">back_clear</router-link> macro tells the stage manager where to draw the background fade effect.
        Right now, the background fade is drawn after all background sprites.
        If we move <router-link to="/docs/back_clear">back_clear</router-link> to BEFORE the 
        <router-link to="/docs/background_define">background_define</router-link> for <code>back_castle_mountains</code>,
        then the mountains would be visible during the fade effect:
      </p>
      <br/>
      <div class="columns">
        <div class="column"><img src="@/assets/Tutorials/tb3_16.png" class="image"/></div>
        <div class="column"><img src="@/assets/Tutorials/tb3_17.png" class="image"/></div>
      </div>
      <br/>

      <p class="title">The Stage Manager</p>
      <p>
        In addition to drawing backgrounds for stages, 
        <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>
        also manages music, custom stage scripts, the day/night cycle, and much more.
      </p>
      <br/>
      <p>
        You can set stage music with the function
        <router-link to="/docs/stage_music_set">stage_music_set</router-link>.
        If you want to define a custom music loop, such as only playing the intro once, 
        you can use the two optional arguments.
      </p>
      <br/>
      <p>
        The <code>stage_script</code> variable can be set to a script which will be automatically run every frame.
        This can be used to create dynamic effects, such as the snow in <code>rm_peak</code>.
      </p>
      <br/>
      <p>
        You can turn on the pre-made day/night cycle with
        <router-link to="/docs/daynight_cycle_enable">setting().daynight_cycle_enable</router-link>.
      </p>
      <br/>

      <p class="title">Summary</p>
      <p>
        - Every stage is a separate room duplicated from <code>rm_stage_template</code> 
      </p>
      <p>
        - Stages are made of "Stage Element" instances
      </p>
      <p>
        - Graphics can be added to stages with Assets Layers, or through code in
        <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>
      </p>
      <p>
        - <router-link to="/docs/obj_stage_manager">obj_stage_manager</router-link>
        can also handle stage music, custom stage scripts, the day/night cycle, etc.
      </p>
      <br/>
      <button class="button">
        <router-link to="/tutorials">
          Back to Tutorials
        </router-link>
      </button>
    </section>
  </div>
</template>